* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #aca8a4;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country-list {
  list-style: none;
}

.country {
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.country-info {
  padding-left: 40px;
}

.country-name {
  letter-spacing: 2px;
  margin: 0;
  font-size: 20px;
  animation: text 3s;
}

@keyframes text {
  0% {
    color: #aca8a4;
  }

  30% {
    letter-spacing: 8px;
  }
}

.capital, .population, .languages {
  color: #f5f243;
  font-size: 16px;
}

.capital-info, .population-info, .languages-info {
  color: #3c3fe6;
  font-size: 16px;
  font-weight: 700;
}

